/* Functional CSS */
.post-work-body {
  width: 100%; }
  .post-work-body .input-description {
    width: 100%; }
  .post-work-body > div {
    width: 50%;
    padding: 60px; }
  .post-work-body .left {
    padding-right: 30px; }
  .post-work-body .right {
    padding-left: 30px; }

.blocks .block-line {
  margin-bottom: 20px; }

input[maxlength],
textarea[maxlength] {
  position: relative; }

.badged[data-remaining] {
  position: relative; }
  .badged[data-remaining]:after {
    content: attr(data-remaining) " / " attr(data-limit);
    display: inline-block;
    position: absolute;
    bottom: 1px;
    right: 1px;
    padding: 0.25ex 0.5ex;
    pointer-events: none;
    /* We aren't interactable, pass through! */
    color: var(--color2);
    opacity: .7;
    font-size: 0.8em; }
  .badged[data-remaining].warning::after {
    color: yellow; }
  .badged[data-remaining].critical::after {
    color: red; }

.uppy-Dashboard-poweredBy {
  display: none !important; }
