/* Functional CSS */


.post-work-body {
    width: 100%;

    .input-description {
        width: 100%;
    }

    >div {
        width: 50%;
        padding: 60px;
    }

    .left {
        padding-right: 30px;
    }

    .right {
        padding-left: 30px;

        form {
            .blocks {}
        }
    }
}

.blocks {
    .block-line {
        margin-bottom: 20px;
    }
}



input[maxlength],
textarea[maxlength] {
    position: relative;
}

.badged[data-remaining] {
    position: relative;

    &:after {
        content: attr(data-remaining) " / "attr(data-limit);
        display: inline-block;
        position: absolute;
        bottom: 1px;
        right: 1px;
        padding: 0.25ex 0.5ex;
        pointer-events: none;
        /* We aren't interactable, pass through! */
        // background-color: rgba(255, 255, 255, 0.75);
        color: var(--color2);
        opacity: .7;
        font-size: 0.8em;
    }

    &.warning::after {
        color: yellow;
    }

    &.critical::after {
        color: red;
    }
}

.uppy-Dashboard-poweredBy {
    display: none !important;
}




